.range-slider {
  font-family: montserrat, Arial, sans-serif;
  font-size: 12px;
}
.range-slider__caption {
  display: flex;
  justify-content: space-between;
  margin-bottom: 19px;
}
.range-slider__caption-text {
  color: #1f2041;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  font-style: inherit;
}
.range-slider__values {
  color: rgba(31, 32, 65, 0.5);
}
.range-slider__desc {
  padding-top: 9px;
  font-size: 12px;
  color: rgba(31, 32, 65, 0.5);
}

.irs--round {
  max-height: 14px;
}
.irs--round .irs-handle {
  width: 16px;
  height: 16px;
  border: 2px solid #fff;
  background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
  border-radius: 50%;
  top: 0px;
  box-shadow: none;
  cursor: pointer;
}
.irs--round .irs-line {
  background-color: #fff;
  border-radius: 3px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  height: 6px;
  top: 5px;
}
.irs--round .irs-bar {
  background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
  top: 6px;
}