$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.range-slider {
  font-family: $toxin-font-montserrat;
  font-size: 12px;

  &__caption {
    display: flex;
    justify-content: space-between;
    margin-bottom: 19px;
  }
  &__caption-text {
    color: $toxin-color-black-100;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    font-style: inherit;
  }
  &__values {
    color: $toxin-color-black-50;
  }

  &__desc {
    padding-top: 9px;
    font-size: 12px;
    color: $toxin-color-black-50;
  }
}
// Modify ion.rangeSlider.css
.irs--round {
  max-height: 14px;
  .irs-handle {
    width: 16px;
    height: 16px;
    border: 2px solid #fff;
    background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
    border-radius: 50%;
    top: 0px;
    box-shadow: none;
    cursor: pointer;
  }
  .irs-line {
    background-color: #fff;
    border-radius: 3px;
    border: 1px solid $toxin-color-black-25;
    height: 6px;
    top: 5px;
  }
  .irs-bar {
    background: linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);
    top: 6px;
  }
}
