.button-simple {
  border: none;
  font-family: montserrat, Arial, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  background: none;
  color: #bc9cff;
  margin: 0px;
  padding: 0;
  cursor: pointer;
  text-transform: uppercase;
}
.button-simple.button-simple_disabled, .button-simple.button-simple_gray-text {
  color: rgba(31, 32, 65, 0.5);
}