* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

html {
  background: #e2e2e2;
  color: #1f2041;
  font-size: 14px;
  font-family: montserrat, Arial, sans-serif;
}

body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.main-content {
  width: 100%;
  min-height: 701px;
  background: url("../../images/backgrounds/sign.jpg") no-repeat;
  padding-top: 20px;
}

.main-content__sign-up-form {
  margin: 0 auto;
  max-width: 380px;
}

.header .header-menu > .header-menu__list > .header-menu__item.header-menu__item:not(:last-of-type) {
  margin-right: 20px;
}

.header .header__header-menu {
  margin-right: 20px;
}