.comfort-status {
  border-bottom: 1px solid rgba(31, 32, 65, 0.1);
  padding-bottom: 20px;
  display: flex;
}
.comfort-status_hide-line {
  border-bottom: none;
}
.comfort-status__desc {
  padding-top: 3px;
  color: rgba(31, 32, 65, 0.75);
}
.comfort-status__caption {
  margin-bottom: 6px;
  font-weight: bold;
}
.comfort-status__text {
  font-size: 14px;
}
.comfort-status__icon {
  font-size: 48px;
  margin-right: 10px;
  color: #bc9cff;
}