.footer {
  background: white;
}
.footer__footer-top {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid rgba(31, 32, 65, 0.1);
}
.footer__footer-top {
  padding: 102px 9.7222222222%;
}
.footer__logo-with-text {
  margin-right: 15px;
  max-width: 260px;
}
.footer__subscribe-area {
  max-width: 260px;
}
.footer__footer-menu {
  margin-left: auto;
}
.footer__footer-menu {
  flex-basis: 600px;
}
.footer__footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 22px 9.7222222222%;
}
.footer__footer-bottom .footer__copyright {
  font-size: 14px;
  line-height: 17px;
  color: rgba(31, 32, 65, 0.75);
  letter-spacing: -0.05px;
}

@media (max-width: 1080px) {
  .footer .footer__footer-top,
.footer .footer__footer-bottom {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media (max-width: 825px) {
  .footer__footer-menu,
.footer__subscribe-area,
.footer__copyright {
    display: none;
  }

  .footer__footer-top {
    border: none;
    justify-content: center;
    padding-bottom: 5px;
    padding-top: 100px;
  }

  .footer__footer-bottom {
    justify-content: center;
    padding-bottom: 110px;
  }

  .footer__logo-with-text {
    margin: 0 auto 17px auto;
  }

  .footer__footer-top .footer__logo-with-text {
    text-align: center;
    width: auto;
    margin-right: 0;
    max-width: 560px;
    margin: 0 auto 17px auto;
  }
}
@media (max-width: 815px) {
  .footer__footer-top {
    padding: 10px 5px;
    min-height: auto;
  }

  .footer__footer-bottom {
    padding: 0 5px 5px;
    min-height: auto;
  }
}