.feedback {
  display: flex;
}
.feedback__left-column {
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.feedback__right-column {
  display: flex;
  flex-direction: column;
}
.feedback__caption {
  padding-top: 3px;
  font-weight: bold;
  font-size: 14px;
}
.feedback__time {
  padding-top: 6px;
  font-size: 14px;
  margin-bottom: 13px;
}
.feedback__text {
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
}
.feedback__wrapper-for-round-image {
  margin-bottom: 11px;
}

.feedback-list__caption {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 19px;
}
.feedback-list__text {
  text-transform: none;
  font-size: 19px;
  font-family: open-sans, Arial, sans-serif;
}
.feedback-list__item:not(:last-child) {
  margin-bottom: 23px;
}
.feedback-list__list {
  list-style: none;
}