$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.image-slider {
  padding: 0;
  width: 100%;
  height: 100%;
  display: block;
  margin: 0 auto;
  position: relative;

  &__input {
    position: absolute;
    width: 1px;
    height: 1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0, 0, 0, 0);
    overflow: hidden;
  }

  &__container {
    display: block;
  }

  &__slide {
    top: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    transform: scale(0);
    transition: all 0.7s ease-in-out;
  }

  &__slide .image-slider__image {
    width: 100%;
    height: 100%;
  }

  &__nav .image-slider__label {
    width: 15.56%;
    height: 100%;
    display: none;
    position: absolute;
    opacity: 0;
    z-index: 9;
    cursor: pointer;
    transition: opacity 0.2s;
    color: #fff;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.3);
    text-shadow: 0px 0px 15px rgb(119, 119, 119);
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
  }

  &_show-nav-btns .image-slider__label,
  &__slide:hover + .image-slider__nav .image-slider__label {
    opacity: 0.5;
  }

  &__nav .image-slider__label:hover {
    opacity: 1;
  }

  &__nav .image-slider__next {
    right: 0;
    background: linear-gradient(270deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
  }

  &__prev:after {
    font-family: $toxin-font-material-icon;
    content: attr(content);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  &__next:after {
    font-family: $toxin-font-material-icon;
    content: attr(content);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  input:checked + .image-slider__container .image-slider__slide {
    opacity: 1;
    transform: scale(1);
    transition: opacity 1s ease-in-out;
  }

  input:checked + .image-slider__container .image-slider__nav .image-slider__label {
    display: block;
  }

  &__nav-dots {
    bottom: 7.8%;
    right: 5.5%;
    display: block;
    position: absolute;
    z-index: 20;
    font-size: 12px;
  }

  &__nav-dots .image-slider__nav-dot {
    width: 8px;
    height: 8px;
    position: relative;
    border-radius: 100%;
    display: inline-block;
    border: 1px solid #fff;
  }

  &__nav-dots .image-slider__nav-dot:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.3);
  }

  @for $i from 1 through 4 {
    input:nth-of-type(#{$i}):checked ~ .image-slider__nav-dots label:nth-of-type(#{$i}) {
      background: #fff;
    }
  }
}
