$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.toggle {
  display: flex;
  height: 20px;

  &__input {
    position: absolute;
    width: 1px;
    height: 1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0, 0, 0, 0);
    overflow: hidden;
  }
  &__label {
    position: relative;
    padding: 0 0 0 50px;
    cursor: pointer;
    font-size: 14px;
    color: $toxin-color-black-75;
  }
  &__label:before {
    box-sizing: border-box;
    content: "";
    position: absolute;
    top: 0px;
    left: 0;
    width: 40px;
    height: 100%;
    border: 1px solid #bc9cff;
    border-radius: 10px;
    transition: 0.2s;
  }
  &__label:after {
    content: "";
    position: absolute;
    top: 4px;
    left: 3px;
    width: 12px;
    height: 12px;
    background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
    border-radius: 100%;
    transition: 0.2s;
  }

  &__input:checked + .toggle__label:after {
    left: 24px;
  }

  .toggle__input:not(:checked) {
    + .toggle__label:after {
      background: $toxin-color-black-25;
    }
    + .toggle__label::before {
      border: 1px solid $toxin-color-black-25;
    }
  }
}
