@font-face {
  font-family: "material-icons";
  font-style: normal;
  font-weight: 400;
  src: url(material-icons-regular.eot);
  /* For IE6-8 */
  src: local("material-icons"), local("material-icons-regular"), url(material-icons-regular.woff2) format("woff2"), url(material-icons-regular.woff) format("woff"), url(material-icons-regular.ttf) format("truetype");
}
.material-icons {
  font-family: "material-icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: "liga";
}