@charset "UTF-8";
.header-menu {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 5px;
  height: 100%;
}
.header-menu__icon {
  display: none;
  cursor: pointer;
  font-family: "material-icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "liga";
}
.header-menu__list {
  list-style: none;
  background: white;
  display: flex;
}
.header-menu__link {
  font-family: montserrat, Arial, sans-serif;
  color: rgba(31, 32, 65, 0.5);
  font-size: 14px;
  text-decoration: none;
  line-height: 36px;
}
.header-menu__link .header-menu__item:not(:last-of-type) {
  padding-right: 10px;
}
.header-menu__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.header-menu__item.header-menu__list_arrow {
  padding-right: 24px;
}
.header-menu > .header-menu__list > .header-menu__item.header-menu__item:not(:last-of-type) {
  margin-right: 30px;
}
.header-menu > .header-menu__list > .header-menu__item.header-menu__item:not(:last-of-type).header-menu__item_sub-menu {
  margin-right: 20px;
}
.header-menu__list:first-child > .header-menu__item:last-child > .header-menu__link {
  padding-right: 0px;
}
.header-menu__list_arrow {
  padding-right: 0px;
}
.header-menu__link + .header-menu__list {
  position: absolute;
  min-width: 150px;
  display: none;
  border-radius: 3px;
  box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.07);
}
.header-menu__link + .header-menu__list > .header-menu__item {
  padding: 0 10px;
}
.header-menu__link + .header-menu__list .header-menu__list {
  position: absolute;
  top: -1px;
  right: -150px;
}
.header-menu__link + .header-menu__list .header-menu__item {
  position: relative;
}
.header-menu__list_arrow {
  position: relative;
}
.header-menu__list_arrow:after {
  content: "";
  font-family: material-icons, Arial, sans-serif;
  text-align: center;
  vertical-align: middle;
  font-size: 24px;
  line-height: 20px;
  bottom: 6px;
  right: 2px;
  color: rgba(31, 32, 65, 0.5);
}
.header-menu__link_bold {
  font-weight: bold;
  color: rgba(31, 32, 65, 0.75);
}
.header-menu__icon.header-menu_show + .header-menu__list {
  transition: transform 600ms;
  transform: translateX(0);
}