@charset "UTF-8";
.subscribe-input {
  font-family: montserrat, Arial, sans-serif;
}
.subscribe-input__caption {
  display: inline-block;
  margin-bottom: 5px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
}
.subscribe-input__input {
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
  background: white;
  padding: 12px 30px 12px 15px;
  font-size: 14px;
  width: 100%;
  font-family: inherit;
}
.subscribe-input__input::placeholder {
  font-size: 14px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.25);
}
.subscribe-input__input:hover, .subscribe-input__input:focus, .subscribe-input_isFocused {
  border: 1px solid rgba(31, 32, 65, 0.5);
}
.subscribe-input__wrapper {
  position: relative;
  overflow: visible;
}
.subscribe-input {
  width: 100%;
}
.subscribe-input__wrapper:after {
  content: "arrow_forward";
  content: "";
  font-family: material-icons, Arial, sans-serif;
  font-size: 24px;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #8ba4f9;
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  font-feature-settings: "liga";
}