$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.expandable-checkbox-list {
  font-family: $toxin-font-montserrat;

  &__caption {
    cursor: pointer;
    position: relative;
    line-height: 24px;
    font-size: 12px;
    font-weight: bold;
    text-transform: uppercase;
    font-family: inherit;
  }
  &__list {
    list-style: none;
    display: none;
  }
  &__item:not(:last-child) {
    margin-bottom: 12px;
  }

  &_show .expandable-checkbox-list__list {
    display: block;
    padding-top: 12px;
  }

  &__caption:after {
    content: "keyboard_arrow_down";
    font-family: $toxin-font-material-icon;
    font-size: 24px;
    font-weight: normal;
    text-transform: none;
    position: absolute;
    color: $toxin-color-black-50;
    right: 11px;
    text-align: center;
  }

  &_show .expandable-checkbox-list__caption:after {
    content: "keyboard_arrow_up";
  }
}
