$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.footer {
  background: white;

  &__footer-top {
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid rgba(31, 32, 65, 0.1);
  }

  &__footer-top {
    padding: 102px 9.722222222222222%;
  }

  &__logo-with-text {
    margin-right: 15px;
    max-width: 260px;
  }

  &__subscribe-area {
    max-width: 260px;
  }

  &__footer-menu {
    margin-left: auto;
  }

  &__footer-menu {
    flex-basis: 600px;
  }

  &__footer-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 22px 9.722222222222222%;
    .footer__copyright {
      font-size: 14px;
      line-height: 17px;
      color: $toxin-color-black-75;
      letter-spacing: -0.05px;
    }
  }
}

@media (max-width: 1080px) {
  .footer {
    .footer__footer-top,
    .footer__footer-bottom {
      padding-left: 5px;
      padding-right: 5px;
    }
  }
}

@media (max-width: 825px) {
  .footer__footer-menu,
  .footer__subscribe-area,
  .footer__copyright {
    display: none;
  }

  .footer__footer-top {
    border: none;
    justify-content: center;
    padding-bottom: 5px;
    padding-top: 100px;
  }
  .footer__footer-bottom {
    justify-content: center;
    padding-bottom: 110px;
  }

  .footer__logo-with-text {
    margin: 0 auto 17px auto;
  }

  .footer__footer-top .footer__logo-with-text {
    text-align: center;
    width: auto;
    margin-right: 0;
    max-width: 560px;
    margin: 0 auto 17px auto;
  }
}

@media (max-width: 815px) {
  .footer__footer-top {
    padding: 10px 5px;
    min-height: auto;
  }
  .footer__footer-bottom {
    padding: 0 5px 5px;
    min-height: auto;
  }
}
