.order-info-form {
  max-width: 380px;
  padding: 40px 5px 30px;
  display: flex;
  justify-content: center;
  background: white;
  padding: 40px 30px 30px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.order-info-form__wrapper-main {
  border: 0 none;
}
.order-info-form__wrapper-main {
  max-width: 320px;
}
.order-info-form__wrapper-for-room-info-caption {
  font-size: 24px;
  margin-bottom: 17px;
}
.order-info-form__item {
  margin-bottom: 20px;
}
.order-info-form__list {
  margin-bottom: 30px;
}
.order-info-form__list, .order-info-form__dates-list {
  list-style: none;
}
.order-info-form__dates-list {
  display: flex;
}
.order-info-form__dates-item:not(:last-child) {
  margin-right: 20px;
}
.order-info-form__cell-tooltip {
  display: flex;
}
.order-info-form__table {
  width: 100%;
  font-size: 14px;
  color: rgba(31, 32, 65, 0.75);
}
.order-info-form__row:nth-child(1) .order-info-form__cell-text, .order-info-form__row:nth-child(2) .order-info-form__cell-text {
  border-bottom: 13px solid transparent;
}
.order-info-form__cell-text {
  width: 214px;
}
.order-info-form__cell-value {
  text-align: end;
  vertical-align: top;
}
.order-info-form__total {
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;
  align-items: baseline;
}
.order-info-form__total-text, .order-info-form__total-value {
  font-size: 19px;
  font-family: open-sans, Arial, sans-serif;
}
.order-info-form__total-text {
  margin-right: 5px;
  text-transform: none;
}
.order-info-form__total-line {
  border-bottom: 1px dotted rgba(31, 32, 65, 0.25);
  margin-right: 5px;
  flex-grow: 1;
}