.sign-up-form {
  padding: 40px 5px 30px;
  max-width: 380px;
  display: flex;
  justify-content: center;
  background: white;
  padding: 40px 30px 30px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.sign-up-form__main-content {
  max-width: 320px;
  border: 0 none;
}
.sign-up-form__caption {
  margin-bottom: 20px;
  font-size: 24px;
  line-height: 30px;
  font-family: open-sans, Arial, sans-serif;
  font-weight: bold;
  color: #1f2041;
}
.sign-up-form__list {
  list-style: none;
  margin-bottom: 24px;
}
.sign-up-form__wrapper {
  margin-bottom: 20px;
}
.sign-up-form__item {
  margin-bottom: 10px;
}
.sign-up-form__wrapper-for-sign-up-button {
  margin-bottom: 30px;
}
.sign-up-form__wrapper-for-sign-in-button {
  height: 44px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sign-up-form__sign-in-text {
  color: #000;
}
.sign-up-form_wrapper-sign-in-button {
  min-width: 99px;
  height: 44px;
}

@media (max-width: 365px) {
  .header__logo {
    display: none;
  }
}