$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.button-bordered {
  border: none;
  font-family: $toxin-font-montserrat;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: #bc9cff;
  margin: 0px;
  padding: 10px 20px;
  cursor: pointer;
  text-transform: uppercase;
  position: relative;
  background: transparent;
  border-radius: 22px;
  z-index: 1;

  &:before,
  &:after {
    content: " ";
    position: absolute;
    border-radius: 22px;
  }

  &:before {
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -2;
    background: #bc9cff;
    background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  }

  &:after {
    top: 2px;
    bottom: 2px;
    left: 2px;
    right: 2px;
    background-color: white;
    z-index: -1;
  }

  &.button-bordered_disabled:before {
    background: linear-gradient(180deg, #bc9cff7f 0%, #8ba4f97f 100%);
  }

  &.button-bordered_dynamic-height {
    height: 100%;
    padding-top: 0;
    padding-bottom: 0;
  }

  &.button-bordered_dynamic-width {
    width: 100%;
    min-width: 78px;
    padding-left: 0;
    padding-right: 0;
  }
}
