.datepicker-input {
  font-family: montserrat, Arial, sans-serif;
}
.datepicker-input__caption {
  display: inline-block;
  margin-bottom: 5px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
}
.datepicker-input__input {
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
  background: white;
  padding: 12px 15px;
  font-size: 14px;
  width: 100%;
  font-family: inherit;
}
.datepicker-input__input::placeholder {
  font-size: 14px;
  color: rgba(31, 32, 65, 0.75);
}
.datepicker-input__input:hover, .datepicker-input__input:focus {
  border: 1px solid rgba(31, 32, 65, 0.5);
}
.datepicker-input__wrapper {
  position: relative;
}
.datepicker-input__wrapper:after {
  content: "keyboard_arrow_down";
  font-family: material-icons, Arial, sans-serif;
  color: rgba(31, 32, 65, 0.5);
  font-size: 24px;
  position: absolute;
  top: 12px;
  right: 9px;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
}