.expandable-checkbox-list {
  font-family: montserrat, Arial, sans-serif;
}
.expandable-checkbox-list__caption {
  cursor: pointer;
  position: relative;
  line-height: 24px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  font-family: inherit;
}
.expandable-checkbox-list__list {
  list-style: none;
  display: none;
}
.expandable-checkbox-list__item:not(:last-child) {
  margin-bottom: 12px;
}
.expandable-checkbox-list_show .expandable-checkbox-list__list {
  display: block;
  padding-top: 12px;
}
.expandable-checkbox-list__caption:after {
  content: "keyboard_arrow_down";
  font-family: material-icons, Arial, sans-serif;
  font-size: 24px;
  font-weight: normal;
  text-transform: none;
  position: absolute;
  color: rgba(31, 32, 65, 0.5);
  right: 11px;
  text-align: center;
}
.expandable-checkbox-list_show .expandable-checkbox-list__caption:after {
  content: "keyboard_arrow_up";
}