$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

/* open-sans-regular - cyrillic-latin */
@font-face {
  font-family: 'open-sans';
  font-style: normal;
  font-weight: 400;
  src: url('regular/open-sans-v17-cyrillic-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('open-sans-regular'), local('OpenSans-Regular'),
       url('regular/open-sans-v17-cyrillic-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('regular/open-sans-v17-cyrillic-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('regular/open-sans-v17-cyrillic-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('regular/open-sans-v17-cyrillic-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('regular/open-sans-v17-cyrillic-latin-regular.svg#OpenSans') format('svg'); /* Legacy iOS */
}
/* open-sans-700 - cyrillic-latin */
@font-face {
  font-family: 'open-sans';
  font-style: normal;
  font-weight: 700;
  src: url('bold/open-sans-v17-cyrillic-latin-700.eot'); /* IE9 Compat Modes */
  src: local('open-sans-bold'), local('OpenSans-Bold'),
       url('bold/open-sans-v17-cyrillic-latin-700.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('bold/open-sans-v17-cyrillic-latin-700.woff2') format('woff2'), /* Super Modern Browsers */
       url('bold/open-sans-v17-cyrillic-latin-700.woff') format('woff'), /* Modern Browsers */
       url('bold/open-sans-v17-cyrillic-latin-700.ttf') format('truetype'), /* Safari, Android, iOS */
       url('bold/open-sans-v17-cyrillic-latin-700.svg#OpenSans') format('svg'); /* Legacy iOS */
}