.guests-input {
  max-width: 320px;
  width: 100%;
  font-family: montserrat, Arial, sans-serif;
}
.guests-input__caption {
  margin-bottom: 5px;
  font-size: 12px;
  font-weight: bold;
  font-family: inherit;
  text-transform: uppercase;
}
.guests-input .guests-input__buttons {
  padding-top: 14px;
  padding-bottom: 2px;
  display: flex;
  justify-content: space-between;
}