.bullet {
  font-family: montserrat, Arial, sans-serif;
  color: rgba(31, 32, 65, 0.75);
  line-height: 24px;
  padding-left: 20px;
  position: relative;
}
.bullet:before {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  background: rgba(31, 32, 65, 0.25);
  border-radius: 50%;
  position: absolute;
  left: 0px;
  top: 7px;
}