.pagination {
  display: inline-flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
}
.pagination__buttons {
  display: flex;
  margin-bottom: 10px;
  width: 100%;
  display: flex;
  justify-content: center;
}
.pagination__list {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
.pagination__list .pagination__item {
  color: #fff;
  color: rgba(31, 32, 65, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-size: 12px;
  padding: 8px 0px;
}
.pagination__list .pagination__item:first-child,
.pagination__list .pagination__item:last-child,
.pagination__list .pagination__item_active {
  width: 40px;
}
.pagination__list .pagination__item:first-child {
  background: #6fcf97;
  color: #fff;
}
.pagination__list .pagination__item:last-child {
  background: #6fcf97;
  color: #fff;
}
.pagination__list .pagination__item.pagination__item_active {
  background-color: #bc9cff;
  font-weight: bold;
  color: #fff;
}
.pagination__list .pagination__item .pagination__link {
  cursor: pointer;
}
.pagination__list .pagination__item_out-of-range .pagination__link {
  cursor: auto;
}
.pagination__caption {
  text-align: center;
  color: rgba(31, 32, 65, 0.75);
}