$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.order-info-form {
  max-width: 380px;
  padding: 40px 5px 30px;
  display: flex;
  justify-content: center;
  background: white;
  padding: 40px 30px 30px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  &__wrapper-main {
    border: 0 none;
  }

  &__wrapper-main {
    max-width: 320px;
  }

  &__wrapper-for-room-info-caption {
    font-size: 24px;
    margin-bottom: 17px;
  }
  &__item {
    margin-bottom: 20px;
  }

  &__list {
    margin-bottom: 30px;
  }

  &__list,
  &__dates-list {
    list-style: none;
  }

  &__dates-list {
    display: flex;
  }

  &__dates-item:not(:last-child) {
    margin-right: 20px;
  }

  &__cell-tooltip {
    display: flex;
  }

  &__table {
    width: 100%;
    font-size: 14px;
    color: $toxin-color-black-75;
  }

  &__row:nth-child(1),
  &__row:nth-child(2) {
    .order-info-form__cell-text {
      border-bottom: 13px solid transparent;
    }
  }

  &__cell-text {
    width: 214px;
  }

  &__cell-value {
    text-align: end;
    vertical-align: top;
  }

  &__total {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
    align-items: baseline;
  }

  &__total-text,
  &__total-value {
    font-size: 19px;
    font-family: $toxin-font-open-sans;
  }

  &__total-text {
    margin-right: 5px;
    text-transform: none;
  }

  &__total-line {
    border-bottom: 1px dotted $toxin-color-black-25;
    margin-right: 5px;
    flex-grow: 1;
  }
}
