.button-bordered {
  border: none;
  font-family: montserrat, Arial, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: #bc9cff;
  margin: 0px;
  padding: 10px 20px;
  cursor: pointer;
  text-transform: uppercase;
  position: relative;
  background: transparent;
  border-radius: 22px;
  z-index: 1;
}
.button-bordered:before, .button-bordered:after {
  content: " ";
  position: absolute;
  border-radius: 22px;
}
.button-bordered:before {
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -2;
  background: #bc9cff;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
}
.button-bordered:after {
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
  background-color: white;
  z-index: -1;
}
.button-bordered.button-bordered_disabled:before {
  background: linear-gradient(180deg, #bc9cff7f 0%, #8ba4f97f 100%);
}
.button-bordered.button-bordered_dynamic-height {
  height: 100%;
  padding-top: 0;
  padding-bottom: 0;
}
.button-bordered.button-bordered_dynamic-width {
  width: 100%;
  min-width: 78px;
  padding-left: 0;
  padding-right: 0;
}