.color-example {
  display: flex;
}
.color-example__squareColor {
  width: 70px;
  height: 70px;
  border-radius: 6px;
  margin-right: 40px;
}
.color-example__squareColor_DS100 {
  background: #1f2041;
}
.color-example__squareColor_DS75 {
  background: rgba(31, 32, 65, 0.75);
}
.color-example__squareColor_DS50 {
  background: rgba(31, 32, 65, 0.5);
}
.color-example__squareColor_DS25 {
  background: rgba(31, 32, 65, 0.25);
}
.color-example__squareColor_DS5 {
  background: rgba(31, 32, 65, 0.05);
}
.color-example__squareColor_Purple {
  background: #bc9cff;
}
.color-example__squareColor_Green {
  background: #6fcf97;
}
.color-example__textDesc {
  vertical-align: middle;
  color: #1f2041;
  font-family: quicksand, Arial, sans-serif;
  font-size: 19px;
  line-height: 29px;
  margin: 6px 0px;
}
.color-example__title {
  font-weight: bold;
}
.color-example__title, .color-example__text {
  font-family: quicksand, Arial, sans-serif;
}