.toggle {
  display: flex;
  height: 20px;
}
.toggle__input {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.toggle__label {
  position: relative;
  padding: 0 0 0 50px;
  cursor: pointer;
  font-size: 14px;
  color: rgba(31, 32, 65, 0.75);
}
.toggle__label:before {
  box-sizing: border-box;
  content: "";
  position: absolute;
  top: 0px;
  left: 0;
  width: 40px;
  height: 100%;
  border: 1px solid #bc9cff;
  border-radius: 10px;
  transition: 0.2s;
}
.toggle__label:after {
  content: "";
  position: absolute;
  top: 4px;
  left: 3px;
  width: 12px;
  height: 12px;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  border-radius: 100%;
  transition: 0.2s;
}
.toggle__input:checked + .toggle__label:after {
  left: 24px;
}
.toggle .toggle__input:not(:checked) + .toggle__label:after {
  background: rgba(31, 32, 65, 0.25);
}
.toggle .toggle__input:not(:checked) + .toggle__label::before {
  border: 1px solid rgba(31, 32, 65, 0.25);
}