.room-reservation-form {
  max-width: 380px;
  padding: 40px 5px 30px;
  display: flex;
  justify-content: center;
  background: white;
  padding: 40px 30px 30px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.room-reservation-form__wrapper-for-inputs, .room-reservation-form__wrapper-for-dates {
  border: 0 none;
}
.room-reservation-form__wrapper-for-inputs {
  max-width: 320px;
}
.room-reservation-form__list {
  list-style: none;
}
.room-reservation-form__title {
  margin-bottom: 20px;
  font-size: 24px;
  line-height: 30px;
  font-family: open-sans, Arial, sans-serif;
  font-weight: bold;
  color: #1f2041;
}
.room-reservation-form__wrapper-for-dates {
  margin-bottom: 21px;
}
.room-reservation-form__dates-list {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  justify-content: space-between;
}
.room-reservation-form__dates-item {
  max-width: 150px;
  width: 46.875%;
}
.room-reservation-form .room-reservation-wrapper-guests-input {
  margin-bottom: 30px;
}