.font-example {
  display: flex;
}
.font-example__title {
  font-family: montserrat, Arial, sans-serif;
  margin-right: 30px;
  color: rgba(31, 32, 65, 0.25);
  text-align: right;
  width: 50px;
}
.font-example__desc {
  color: #1f2041;
  font-family: inherit;
}
.font-example_h1 {
  font-family: quicksand, Arial, sans-serif;
  font-size: 24px;
  line-height: 30px;
}
.font-example_h1 .font-example__desc {
  width: 351px;
  font-weight: bold;
}
.font-example_h2 {
  font-family: quicksand, Arial, sans-serif;
  font-size: 19px;
  line-height: 24px;
}
.font-example_h2 .font-example__desc {
  width: 282px;
  font-weight: bold;
}
.font-example_h3 {
  font-family: montserrat, Arial, sans-serif;
  font-size: 12px;
  line-height: 15px;
}
.font-example_h3 .font-example__desc {
  width: 186px;
  font-weight: bold;
}
.font-example_body {
  font-family: montserrat, Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
}
.font-example_body .font-example__desc {
  width: 301px;
  color: rgba(31, 32, 65, 0.75);
}