.checkboxes-list {
  font-family: montserrat, Arial, sans-serif;
}
.checkboxes-list__list {
  list-style: none;
}
.checkboxes-list__caption {
  margin-bottom: 16px;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  font-family: inherit;
}
.checkboxes-list__item:not(:last-child) {
  margin-bottom: 13px;
}