$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70px;
  padding: 0px 9.722222%;
  background: #fff;

  &__wrapper-main {
    display: flex;
    align-items: center;
    flex-basis: 80.55555555555556%;
  }
  &__logo {
    margin-right: 15px;
  }

  &__header-menu {
    margin-left: auto;
    margin-right: 2.586206896551724%;
    align-self: stretch;
    z-index: 100;
  }

  &__auth-area {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &_auth-true .header__auth-area {
    justify-content: flex-end;
    flex-basis: auto;
  }

  &__button-bordered {
    height: 100%;
    margin-right: 20px;
  }

  &__button-filled {
    height: 34px;
  }

  &__auth-name {
    display: none;
    font-family: $toxin-font-montserrat;
    color: $toxin-color-black-50;
    border-left: 1px solid $toxin-color-black-10;
    align-items: center;
    padding-left: 30px;
    text-align: center;
    font-size: 14px;
    line-height: 17px;
  }

  &_auth-true .header__auth-name {
    display: flex;
  }

  &_auth-true .header__button-bordered,
  &_auth-true .header__button-filled {
    display: none;
  }

  &_auth-true .header__button-bordered,
  &_auth-true .header__wrapper-auth-register {
    display: none;
  }
}

@media (max-width: 888px) {
  .header {
    padding: 0 5px;
  }
  .header .header__logo {
    margin-right: 5px;
  }
  .header .header__header-menu {
    margin-right: 5px;
  }
}

@media (max-width: 715px) {
  .header {
    justify-content: space-between;
    padding: 0;
    padding-right: 5px;
  }
  .header__header-menu {
    order: -1;
    margin-left: 0;
  }
  .header-menu__icon {
    display: block;
  }
  .header-menu > .header-menu__list {
    padding: 0 5px;
    flex-wrap: nowrap;
    flex-direction: column;
    position: absolute;
    left: 0px;
    top: 100%;
    overflow: hidden;
    transform: translateX(-100%);
  }
  .header-menu__icon.header-menu_hide + .header-menu__list {
    transition: transform 400ms 0ms;
  }
  .header-menu__icon.header-menu_show + .header-menu__list .header-menu__list {
    position: static;
  }
}

@media (max-width: 488px) {
  .header__button-bordered {
    margin-right: 5px;
    margin-bottom: 5px;
  }
  .header__auth-area {
    flex-direction: column;
    margin: 5px 0;
  }
}
