.checkbox__input {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.checkbox__input:after {
  display: block;
  position: absolute;
}
.checkbox__label {
  display: inline-block;
  position: relative;
  padding-left: 30px;
  line-height: 18px;
  cursor: pointer;
  color: rgba(31, 32, 65, 0.5);
  font-size: 14px;
}
.checkbox__label:before {
  box-sizing: border-box;
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 20px;
  height: 20px;
  border: 1px solid #bc9cff;
  background-color: #fff;
  border-radius: 4px;
}
.checkbox__label:after {
  box-sizing: border-box;
  content: "";
  position: absolute;
  transition: all 0.2s ease;
  left: 6px;
  top: 6px;
  width: 9px;
  height: 7px;
  border-radius: 2px;
  border-left: 3px solid #8ba4f9;
  border-bottom: 3px solid #8ba4f9;
  transform: rotate(-45deg);
}
.checkbox__input:not(:checked) + .checkbox__label:after {
  opacity: 0;
}
.checkbox__input:checked + .checkbox__label:after {
  opacity: 1;
}
.checkbox__desc {
  padding-left: 30px;
  font-size: 12px;
  color: rgba(31, 32, 65, 0.5);
}
.checkbox__label_bold {
  font-weight: bold;
  color: rgba(31, 32, 65, 0.75);
}
.checkbox__label.checkbox__label_bold + .checkbox__desc {
  padding-top: 3px;
}