.sign-in-form {
  max-width: 380px;
  padding: 40px 5px 30px;
  display: flex;
  justify-content: center;
  background: white;
  padding: 40px 30px 30px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.sign-in-form__fieldset {
  max-width: 320px;
  width: 100%;
  border: 0 none;
}
.sign-in-form__caption {
  margin-bottom: 20px;
  font-size: 24px;
  line-height: 30px;
  font-family: open-sans, Arial, sans-serif;
  font-weight: bold;
  color: #1f2041;
}
.sign-in-form__list {
  list-style-type: none;
  margin-bottom: 20px;
}
.sign-in-form__item {
  margin-bottom: 10px;
}
.sign-in-form__button-arrow {
  margin-bottom: 30px;
}
.sign-in-form__wrapper-for-sign-in-button {
  height: 44px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sign-in-form__sign-in-text {
  color: #000;
}
.sign-in-form__wrapper-sign-up-button {
  height: 44px;
}