.rate-button {
  display: inline-flex;
  flex-direction: row-reverse;
}
.rate-button__input {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.rate-button > .rate-button__label {
  cursor: pointer;
  color: #bc9cff;
}
.rate-button__label:after {
  content: "star_border";
}
.rate-button__input:checked ~ .rate-button__label:after {
  content: "star";
}
.rate-button:hover > .rate-button__label:after {
  content: "star_border";
}
.rate-button > .rate-button__label:hover:after, .rate-button__label:hover ~ .rate-button__label:after {
  content: "star";
}
.rate-button.rate-button_disabled {
  pointer-events: none;
}