$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.comfort-input {
  font-family: $toxin-font-montserrat;

  &__caption {
    font-size: 12px;
    line-height: 15px;
    margin-bottom: 5px;
    text-transform: uppercase;
    font-weight: bold;
    font-family: inherit;
  }
}

.iqdropdown {
  border: 1px solid $toxin-color-black-25;
  display: flex;
  font-family: $toxin-font-montserrat;
  padding: 12px 44px 12px 15px;
  height: auto;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;

  &::after {
    position: absolute;
    font-family: $toxin-font-material-icon;
    font-size: 24px;
    content: "keyboard_arrow_down";
    display: inline-block;
    right: 9px;
    bottom: 8px;
    color: $toxin-color-black-75;
  }

  .iqdropdown-menu {
    outline: 1px solid rgba(31, 32, 65, 0.5);
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    padding: 6px 7px 8px 15px;
    position: absolute;
    left: 1px;
    top: 43px;
    transition: all 0.2s ease-in-out;
    width: 100%;
    box-shadow: none;
  }

  .iqdropdown-selection {
    font-size: 14px;
    color: $toxin-color-black-75;
    font-family: inherit;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 45px;
  }
  .iqdropdown-selection::after {
    background-image: none;
  }
  &.menu-open .iqdropdown-selection::after {
    background-image: none;
  }

  .iqdropdown-item {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    font-family: inherit;
  }

  .iqdropdown-item-controls {
    height: auto;
  }

  .iqdropdown-menu-option {
    height: auto;
    padding: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .iqdropdown-menu-option:not(:last-child) {
    margin-bottom: 7px;
  }

  .iqdropdown-item-controls button.button-decrement,
  .iqdropdown-item-controls button.button-increment {
    border-bottom-left-radius: 0px;
    border-top-left-radius: 0px;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    padding: 0px;
  }

  .iqdropdown-item-controls .counter {
    border: none;
    padding: 7px 12px;
    font-size: 12px;
    font-weight: bold;
    font-family: inherit;
  }

  .icon-decrement {
    display: flex;
    justify-content: center;
  }

  .icon-decrement::after {
    background: $toxin-color-black-50;
    height: 1px;
    width: 8px;
  }

  .icon-decrement.icon-increment::before {
    height: 1px;
    width: 8px;
  }
}
