$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.subscribe-input {
  font-family: $toxin-font-montserrat;

  &__caption {
    display: inline-block;
    margin-bottom: 5px;
    font-size: 12px;
    font-weight: bold;
    text-transform: uppercase;
  }

  &__input {
    border: 1px solid $toxin-color-black-25;
    border-radius: 4px;
    background: white;
    padding: 12px 30px 12px 15px;
    font-size: 14px;
    width: 100%;
    font-family: inherit;
  }

  &__input::placeholder {
    font-size: 14px;
    line-height: 18px;
    color: $toxin-color-black-25;
  }
  &__input:hover,
  &__input:focus,
  &_isFocused {
    border: 1px solid $toxin-color-black-50;
  }

  &__wrapper {
    position: relative;
    overflow: visible;
  }

  & {
    width: 100%;
  }

  &__wrapper:after {
    content: "arrow_forward";
    content: "\e5c8";
    font-family: $toxin-font-material-icon;
    font-size: 24px;
    background: linear-gradient(180deg, $toxin-color-purple 0%, $toxin-color-blue 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    color: $toxin-color-blue;
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    font-feature-settings: "liga";
  }
}
