.like-button__input {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
.like-button__count {
  font-size: 10px;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 10px;
  color: rgba(31, 32, 65, 0.25);
  padding: 2px 5px 3px;
  min-width: 40px;
  cursor: pointer;
}
.like-button__count:before {
  font-family: material-icons, Arial, sans-serif;
  content: "favorite_border";
  margin-right: 5px;
}
.like-button__input:checked + .like-button__count {
  border: 1px solid #bc9cff;
  color: #bc9cff;
}
.like-button__input:checked + .like-button__count:before {
  content: "favorite";
}