$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.pagination {
  display: inline-flex;
  align-items: center;
  flex-direction: column;
  width: 100%;

  &__buttons {
    display: flex;
    margin-bottom: 10px;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  &__list {
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;

    .pagination__item {
      color: #fff;
      color: $toxin-color-black-50;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      font-size: 12px;
      padding: 8px 0px;
    }
    .pagination__item:first-child,
    .pagination__item:last-child,
    .pagination__item_active {
      width: 40px;
    }

    .pagination__item:first-child {
      background: $toxin-color-green;
      color: #fff;
    }
    .pagination__item:last-child {
      background: $toxin-color-green;
      color: #fff;
    }

    .pagination__item.pagination__item_active {
      background-color: $toxin-color-purple;
      font-weight: bold;
      color: #fff;
    }
    .pagination__item .pagination__link {
      cursor: pointer;
    }
    .pagination__item_out-of-range .pagination__link {
      cursor: auto;
    }
  }

  &__caption {
    text-align: center;
    color: $toxin-color-black-75;
  }
}
