.room-info-caption {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  font-size: inherit;
}
.room-info-caption__caption-left-part {
  font-size: inherit;
  color: #1f2041;
}
.room-info-caption__caption-n {
  font-family: quicksand, Arial, sans-serif;
  font-size: 14px;
  font-weight: bold;
  margin-right: 5px;
}
.room-info-caption__caption-room-number {
  font-family: quicksand, Arial, sans-serif;
  margin-right: 5px;
  font-size: inherit;
  font-weight: bold;
}
.room-info-caption__room-type {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  color: #bc9cff;
}
.room-info-caption__cost-per-day {
  color: rgba(31, 32, 65, 0.5);
  font-weight: bold;
}
.room-info-caption__cost-per-day-value {
  margin-right: 5px;
  font-size: 14px;
}
.room-info-caption__cost-per-day-text {
  font-weight: normal;
  font-size: 12px;
}