.radios-list {
  font-family: montserrat, Arial, sans-serif;
}
.radios-list__list {
  list-style: none;
  display: flex;
}
.radios-list__caption {
  margin-bottom: 16px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  font-family: inherit;
}
.radios-list__item:not(:last-child) {
  margin-right: 20px;
}