.button-arrow {
  position: relative;
  border: none;
  font-family: montserrat, Arial, sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  margin: 0px;
  padding: 15px 20px;
  cursor: pointer;
  text-transform: uppercase;
  width: 100%;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  border-radius: 22px;
  color: #ffffff;
}
.button-arrow:after {
  font-family: material-icons, Arial, sans-serif;
  content: "arrow_forward";
  position: absolute;
  right: 10px;
  font-weight: normal;
  font-size: 24px;
  text-transform: none;
}