.masked-input {
  font-family: montserrat, Arial, sans-serif;
}
.masked-input__caption {
  display: block;
  margin-bottom: 5px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
}
.masked-input__input {
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
  background: white;
  padding: 13px 15px 13px;
  width: 100%;
  outline: none;
  font-family: inherit;
}
.masked-input__input::placeholder {
  font-size: 14px;
  color: rgba(31, 32, 65, 0.25);
}
.masked-input__input:hover, .masked-input__input:focus {
  border: 1px solid rgba(31, 32, 65, 0.5);
}