$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.checkbox {
   &__input {
      position: absolute;
      width: 1px;
      height: 1px;
      border: 0;
      padding: 0;
      white-space: nowrap;
      clip-path: inset(100%);
      clip: rect(0, 0, 0, 0);
      overflow: hidden;
   }

   &__input:after {
      display: block;
      position: absolute;
   }

   &__label {
      display: inline-block;
      position: relative;
      padding-left: 30px;
      line-height: 18px;
      cursor: pointer;
      color: $toxin-color-black-50;
      font-size: 14px;
   }

   &__label:before {
      box-sizing: border-box;
      content: "";
      position: absolute;
      left: 0px;
      top: 0px;
      width: 20px;
      height: 20px;
      border: 1px solid $toxin-color-purple;
      background-color: #fff;
      border-radius: 4px;
   }

   &__label:after {
      box-sizing: border-box;
      content: "";
      position: absolute;
      transition: all 0.2s ease;
      left: 6px;
      top: 6px;
      width: 9px;
      height: 7px;
      border-radius: 2px;
      border-left: 3px solid #8ba4f9;
      border-bottom: 3px solid #8ba4f9;
      transform: rotate(-45deg);
   }

   &__input:not(:checked) + .checkbox__label:after {
      opacity: 0;
   }

   &__input:checked + .checkbox__label:after {
      opacity: 1;
   }

   &__desc {
      padding-left: 30px;
      font-size: 12px;
      color: $toxin-color-black-50;
   }

   &__label_bold {
      font-weight: bold;
      color: $toxin-color-black-75;
   }

   &__label.checkbox__label_bold + .checkbox__desc {
      padding-top: 3px;
   }
}
