$toxin-color-black-100: #1f2041;
$toxin-color-black-75: rgba(31, 32, 65, 0.75);
$toxin-color-black-50: rgba(31, 32, 65, 0.5);
$toxin-color-black-10: rgba(31, 32, 65, 0.1);
$toxin-color-black-25: rgba(31, 32, 65, 0.25);
$toxin-color-black-5: rgba(31, 32, 65, 0.05);
$toxin-color-purple: #bc9cff;
$toxin-color-green: #6fcf97;
$toxin-color-blue: #8ba4f9;
$toxin-color-gray: #e5e5e5;
$toxin-color-light-gray: #f9f9f9;
$toxin-font-material-icon: material-icons, Arial, sans-serif;
$toxin-font-montserrat: montserrat, Arial, sans-serif;
$toxin-font-open-sans: open-sans, Arial, sans-serif;
$toxin-font-quicksand: quicksand, Arial, sans-serif;
$toxin-font-awesome: awesome-5-brands-regular-400, Arial, sans-serif;

@mixin reset {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

@mixin define-html {
  background: #e2e2e2;
  color: $toxin-color-black-100;
  font-size: 14px;
  font-family: $toxin-font-montserrat;
}

@mixin define-body {
  margin: 0 auto;
  min-width: 320px;
  max-width: 1440px;
  background: #fff;
}

.header-menu {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 5px;
  height: 100%;

  &__icon {
    display: none;
    cursor: pointer;
    font-family: "material-icons";
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: grayscale;
    font-feature-settings: "liga";
  }

  &__list {
    list-style: none;
    background: white;
    display: flex;
  }

  &__link {
    font-family: $toxin-font-montserrat;
    color: $toxin-color-black-50;
    font-size: 14px;
    text-decoration: none;
    line-height: 36px;
  }

  &__link .header-menu__item:not(:last-of-type) {
    padding-right: 10px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__item.header-menu__list_arrow {
    padding-right: 24px;
  }
  & > .header-menu__list > .header-menu__item.header-menu__item:not(:last-of-type) {
    margin-right: 30px;
    &.header-menu__item_sub-menu {
      margin-right: 20px;
    }
  }

  &__list:first-child > .header-menu__item:last-child > .header-menu__link {
    padding-right: 0px;
  }
  &__list_arrow {
    padding-right: 0px;
  }

  &__link + .header-menu__list {
    position: absolute;
    min-width: 150px;
    display: none;
    border-radius: 3px;
    box-shadow: 0 6px 16px 0 rgba(0, 0, 0, 0.07);
  }

  &__link + .header-menu__list > .header-menu__item {
    padding: 0 10px;
  }

  &__link + .header-menu__list .header-menu__list {
    position: absolute;
    top: -1px;
    right: -150px;
  }

  &__link + .header-menu__list .header-menu__item {
    position: relative;
  }

  &__list_arrow {
    position: relative;
  }

  &__list_arrow:after {
    content: "\E313";
    font-family: $toxin-font-material-icon;
    text-align: center;
    vertical-align: middle;
    font-size: 24px;
    line-height: 20px;
    bottom: 6px;
    right: 2px;
    color: rgba(31, 32, 65, 0.5);
  }

  &__link_bold {
    font-weight: bold;
    color: $toxin-color-black-75;
  }

  &__icon.header-menu_show + .header-menu__list {
    transition: transform 600ms;
    transform: translateX(0);
  }
}
