.text-input {
  width: 100%;
  font-family: montserrat, Arial, sans-serif;
}
.text-input__caption {
  margin-bottom: 5px;
  display: inline-block;
  font-family: inherit;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
}
.text-input__input {
  color: rgba(31, 32, 65, 0.75);
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
  background: white;
  padding: 12px 15px;
  outline: none;
  width: 100%;
  font-size: 14px;
  font-family: inherit;
}
.text-input__input::placeholder {
  color: rgba(31, 32, 65, 0.25);
}
.text-input__input:hover, .text-input__input:focus, .text-input_isFocused .text-input__input {
  border: 1px solid rgba(31, 32, 65, 0.5);
}